<template>
  <div>
    <div class="bg-white vw-99 vh-100">
      <nav class="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
        <a class="navbar-brand font-weight-bold col-sm-3 col-md-2 mr-0 text-white"
          >Patient Condition</a>
        <ul class="navbar-nav px-3">
          <li class="nav-item text-nowrap">
            <router-link to="/" class="nav-link">Log out</router-link>
          </li>
        </ul>
      </nav>
      <div class="container-fluid bg-white">
        <div class="row">
          <main role="main" class="col-12 px-4">
            <div class="mt-md-5 mt-0 mx-auto col-12 col-lg-8">
              <form @submit.prevent>
                <label for="patient-data" class="h6">Choose Patient</label>
                <select class="custom-select" id="patient-data" @change="getPatientData">
                  <option selected disabled> ---------- Choose Paitent ----------</option>
                  <option :value="userID" v-for="(patient, id) in patients" :key="id" :id="patient">
                    userID: {{patient.value}} | Name: Person{{id + 1}}
                  </option>
                </select>
              </form>
              <!-- class="text-danger" -->
              <p class="my-5" :class = "{'text-success':patientOnBedData[0] === 1}"
                v-show="isShowPatientData">
                <span class="c-patient-signal"
                  :class="{'bg-success':patientOnBedData[0] === 1}">
                </span>
                Patient on bed
              </p>
            </div>
              <div id="patient-table"  v-show="isShowPatientData">
                <div v-if="fakePatientInfo" class="mt-0 mx-auto col-12 col-lg-8">
                  <div class="table-responsive">
                    <table class="table table-striped table-sm">
                      <thead>
                        <tr>
                          <th>UserID</th>
                          <th>Photo</th>
                          <th>Age</th>
                          <th>Gender</th>
                          <th>Cell</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>{{chosenPatient}}</td>
                          <td>
                            <img :src="fakePatientInfo.picture.large" alt="fake">
                          </td>
                          <td>{{fakePatientInfo.dob.age}}</td>
                          <td>{{fakePatientInfo.gender}}</td>
                          <td>{{fakePatientInfo.cell}}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
              <!-- line-chart -->
              <div v-show="isShowPatientData" id="data-img">
                <div class="col-lg-8 col-12 mx-auto">
                  <canvas class="my-4 w-100" id="breathe-chart"></canvas>
                </div>
                <div class="col-lg-8 col-12 mx-auto">
                  <canvas class="my-4 w-100" id="heartbeat-chart"></canvas>
                </div>
                <div class="col-lg-8 col-12 mx-auto my-5">
                  <div class="c-modeCheckBox">
                    <label class="mx-1" for="historymode">Check history map</label>
                    <input class="c-history-input" type="checkbox" id="historymode"
                      v-model="isHistoryMode">
                  </div>
                </div>
                <div class="col-lg-8 col-12 mx-auto my-5" id="pressure-map-container"
                  v-show="isHistoryMode === false">
                  <div id="pressure-map" class="c-pressure-map d-flex justify-content-center"></div>
                </div>
                <div class="col-lg-8 col-12 mx-auto my-5" id="pressure-map-container"
                  v-show="isHistoryMode === true">
                  <div id="pressure-history-map"
                  class="c-pressure-map d-flex justify-content-center"></div>
                  <form>
                    <input v-model="historyValue" type="range" class="custom-range my-3"
                      max="3600" min="0" @input="getPatientData('history');setTime()" id="range">
                    <p class="mx-auto my-3 text-center h4">{{fullDate[0]}} | {{currentTime}}</p>
                  </form>
                </div>
              </div>
          </main>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Chart from 'chart.js';
import d3 from 'd3';

/* eslint-disable */
export default {
  computed: {
    userID() {
      return JSON.parse(localStorage.getItem('userID')).userID;
    },
  },
  data() {
    return {
      isPatientExist: false,
      PatientExistData: 0,
      admin: {
        adminView: null,
        // adminId: null,
      },
      patientGroup: {
        all: [],
        group1: [],
        group2: [
          { value: 'fake1' },
          { value: 'fake2' },
          { value: 'fake3' },
          { value: 'fake4' },
          { value: 'fake5' },
          { value: 'fake6' },
        ],
      },
      patients: [],
      adminView: [],
      fakePatientInfo: null,
      chosenPatient: '',
      isMapExist: false,
      chart: {
        xAxis: [],
        datas: [],
      },
      chart2: {
        xAxis2: [],
        datas2: [],
      },
      fullDate: [],
      fullTime: [],
      fullValue: [],
      patientOnBedData: [],
      patientOnBedData2: [],
      historyValue: 0,
      isHistoryMode: false,
      isShowPatientData: false,
      currentTime: null,
    };
  },
  methods: {
    getPatinetsInfo() {
      const vm = this;
      const cors = 'https://cors-anywhere.herokuapp.com/';
      // const url = 'http://18.223.16.57/api/v1/series/userID';
      const url = 'http://52.15.225.56/api/v1/series/userID';
      const auth = JSON.parse(localStorage.getItem('token')).token;
      const xhr = new XMLHttpRequest();
      const apiError = setTimeout(() => {
        // eslint-disable-next-line
        alert('API Error');
      }, 5000);
      xhr.open('get', cors + url);
      xhr.setRequestHeader('Authorization', `Bearer ${auth}`);
      xhr.send(auth);
      xhr.onloadend = () => {
        clearTimeout(apiError);
        vm.patientGroup.group1 = JSON.parse(xhr.response).data;
        // vm.getFakePatients();
        vm.patientGroup.all = vm.patientGroup.group1.concat(vm.patientGroup.group2);
        vm.setAdminView();
        console.log(vm.patientGroup)
      };
    },
    getpatientApi() {
      const vm = this;
      const cors = 'https://cors-anywhere.herokuapp.com/';
      // const url = 'http://18.223.16.57/api/v1/series/bedStatus?startTimestamp=1564744300&endTimestamp=1565450241&userID=9999';
      const url = 'http://52.15.225.56/api/v1/series/bedStatus?startTimestamp=1566864000&endTimestamp=1566950400&userID=9999';
      const xhr = new XMLHttpRequest;
      const auth = JSON.parse(localStorage.getItem('token')).token;
      xhr.open('get', cors + url);
      xhr.setRequestHeader('Authorization', `Bearer ${auth}`);
      xhr.send(null);
      xhr.onloadend = () => {
        if (!JSON.parse(xhr.response).data) {
          // eslint-disable-next-line
          alert('API ERROR');
          return;
        }
        // console.log(xhr.response)
        JSON.parse(xhr.response).data.forEach((el) => {
          vm.fullDate.push(el.time.split('T')[0]);
          vm.fullTime.push(el.time.split('T')[1].split('Z')[0]);
          vm.fullValue.push(el.value);
          vm.patientOnBedData.push(el.value);
          // heart
          vm.patientOnBedData2.push(el.value);
        });
      };
    },
    setTime() {
      const value = document.querySelector('#range').value;
      this.currentTime = this.fullTime[value];
    },
    getPatientData(isHistory) {
      const vm = this;
      // pressureMap function
      function pressureMapData() {
        const pressureMapAry = [];
        let xpos = 1;
        let ypos = 1;
        const width = 36;
        const height = 36;
        for (let rowLine = 0; rowLine < 16; rowLine++) {
          pressureMapAry.push([]);
          for (let colLine = 0; colLine < 16; colLine++) {
            pressureMapAry[rowLine].push({
              x: xpos,
              y: ypos,
              width: width,
              height: height,
            });
            xpos += width;
          }
          xpos = 1;
          ypos += height;
        }
        return pressureMapAry;
      }
      function getPressureMapStatus() { // 取得壓力圖資料
        const rectAll = document.querySelectorAll('.square');
        rectAll.forEach((rect) => {
          if (vm.patientOnBedData[0] === 1) {
            rect.dataset.pressure = Math.floor(Math.random() * 100);
            if (rect.dataset.pressure >= 75) {
              rect.style.fill = 'rgba(255, 0, 0, 0.1)';
            } else if (rect.dataset.pressure >= 50 && rect.dataset.pressure <= 75) {
              rect.style.fill = 'rgba(255, 165, 0, 0.2)';
            } else if (rect.dataset.pressure >= 25 && rect.dataset.pressure <= 50) {
              rect.style.fill = 'rgba(0, 0, 255, 0.1)';
            } else {
              rect.style.fill = 'rgba(0, 128, 0, 0.1)';
            }
          } else if (!rect.style.fill){
            rect.style.fill = 'transparent';
          }
        });
      }
      function getHisPressureMapStatus() { // 取得過往壓力圖資料
        const rectAll = document.querySelectorAll('.his-square');
        rectAll.forEach((rect) => {
          if (vm.patientOnBedData[0] === 1) {
            rect.dataset.pressure = Math.floor(Math.random() * 100);
            if (rect.dataset.pressure >= 75) {
              rect.style.fill = 'rgba(255, 0, 0, 0.1)';
            } else if (rect.dataset.pressure >= 50 && rect.dataset.pressure <= 75) {
              rect.style.fill = 'rgba(255, 165, 0, 0.2)';
            } else if (rect.dataset.pressure >= 25 && rect.dataset.pressure <= 50) {
              rect.style.fill = 'rgba(0, 0, 255, 0.1)';
            } else {
              rect.style.fill = 'rgba(0, 128, 0, 0.1)';
            }
          } else if (!rect.style.fill){
            rect.style.fill = 'transparent';
          }
        });
      }
      if (isHistory === 'history') {
        clearInterval(reUpdatePressureMapStatus);
        getHisPressureMapStatus();
        return;
      }
      // chart-part 在床
      const ctx = document.getElementById('breathe-chart');
      const xAxis = vm.chart.xAxis;
      const datas = vm.chart.datas;
      let initTime = 0;
      let initDate = 0;
      let initValue = 0;
      const chart = new Chart(ctx, { // chart本體
        type: 'line',
        data: {
          labels: xAxis,
          datasets: [
            {
              label: 'Breathe(1 means on bed)',
              data: datas,
              backgroundColor: 'rgba(0, 123, 255, 0.5)',
            },
          ],
        },
        responsive: true,
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  suggestedMin: -1,
                  suggestedMax: 2,
                  beginAtZero: true,
                  stepSize: 1,
                },
                scaleLabel: {
                  display: true,
                  labelString: 'Detect value',
                },
              },
            ],
          },
        },
      });
      // 心跳
      const ctx2 = document.getElementById('heartbeat-chart');
      const xAxis2 = vm.chart2.xAxis2;
      const datas2 = vm.chart2.datas2;
      let initTime2 = 0;
      let initDate2 = 0;
      let initValue2 = 0;
      const chart2 = new Chart(ctx2, { // chart本體
        type: 'line',
        data: {
          labels: xAxis2,
          datasets: [
            {
              label: 'Hearbeat',
              data: datas2,
              backgroundColor: 'rgba(0, 123, 255, 0.5)',
            },
          ],
        },
        responsive: true,
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  suggestedMin: 0,
                  suggestedMax: 100,
                  beginAtZero: true,
                },
                scaleLabel: {
                  display: true,
                  labelString: 'Detect value',
                },
              },
            ],
          },
        },
      });
      // pressureMap part
      const pressureMap = d3.select('#pressure-map')
        .append('svg')
        .attr({
          width: `${(36 * 16 + 2)}px`,
          height: `${36 * 16 + 2}px`,
        })
        .attr('class', 'pressure-map-svg');
      const row = pressureMap.selectAll('.row')
        .data(pressureMapData)
        .enter()
        .append('g')
        .attr('class', 'row');
      row.selectAll('.square')
        .data(d => d)
        .enter().append('rect')
        .attr('class', 'square')
        .attr('x', d => d.x)
        .attr('y', d => d.y)
        .attr('width', d => d.width)
        .attr('height', d => d.height)
        .style('fill', 'transparent')
        .style('stroke', 'rgba(0, 0, 0, 0.05)');

      // pressureMap history part
      const pressureHisMap = d3.select('#pressure-history-map')
        .append('svg')
        .attr({
          width: `${(36 * 16 + 2)}px`,
          height: `${36 * 16 + 2}px`,
        })
        .attr('class', 'pressure-map-svg');
      const Historyrow = pressureHisMap.selectAll('.his-row')
        .data(pressureMapData)
        .enter()
        .append('g')
        .attr('class', 'his-row');
      Historyrow.selectAll('.square')
        .data(d => d)
        .enter().append('rect')
        .attr('class', 'his-square')
        .attr('x', d => d.x)
        .attr('y', d => d.y)
        .attr('width', d => d.width)
        .attr('height', d => d.height)
        .style('fill', 'transparent')
        .style('stroke', 'rgba(0, 0, 0, 0.05)');

      // chart function
      // breath
      function appendXAxis() { // 移除 chart 前面的資料表現
        const date = new Date();
        if (xAxis.length > 10) {
          xAxis.shift();
          datas.shift();
        }
        initTime ++
        xAxis.push(vm.fullTime[initTime]);
        //假資料
        // xAxis.push( // 取得現在時間 並加入 chart 的 x軸
        //   `${date.getHours() < 10 ? `0${date.getHours()}` : date.getHours()}:${
        //     date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()
        //   }:${date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds()}`,
        // );
      }
      function appendDatas() { // 增加 chart 資料
        initValue ++
        datas.push(vm.fullValue[initValue]);
        vm.patientOnBedData.splice(0, 1);
      }
      // heart
      function appendXAxis2() { // 移除 chart 前面的資料表現
        const date2 = new Date();
        if (xAxis2.length > 10) {
          xAxis2.shift();
          datas2.shift();
        }
        initTime2 ++
        xAxis2.push(vm.fullTime[initTime]);
      }
      function appendDatas2() { // 增加 chart 資料
        let value;
        function count(min, max) {
          value = Math.floor(Math.random() * (max - min + 1)) + min;
        }
        count(50, 80);
        datas2.push(value);
        vm.patientOnBedData2.splice(0, 1);
      }
      function updateChart() { // 持續更新 chart 資料
        appendXAxis();
        appendDatas();
        appendXAxis2();
        appendDatas2();
        chart.update();
        chart2.update();
      }
      // 動態更新資料
      const reUpdateChart = setInterval(() => updateChart(), 1000);
      const reUpdatePressureMapStatus = setInterval(() => getPressureMapStatus(), 1750);
      // get api
      const cors = 'https://cors-anywhere.herokuapp.com/';
      const url = 'https://randomuser.me/api/';
      const elPressureMap = document.getElementById('pressure-map');
      const elHisPressureMap = document.getElementById('pressure-history-map');
      const xhr = new XMLHttpRequest();
      let info;
      vm.$store.dispatch('loadingOverlay');
      xhr.open('get', cors + url);
      xhr.send(null);
      xhr.onloadend = () => {
        vm.isShowPatientData = true;
        vm.chart.xAxis = [];
        vm.chart.datas = [];
        info = JSON.parse(xhr.response);
        vm.fakePatientInfo = { ...info.results[0] };
        vm.$store.dispatch('loadingOverlay');
        if (vm.isMapExist) {
          vm.historyValue = 0;
          getHisPressureMapStatus();
          clearInterval(reUpdatePressureMapStatus);
          clearInterval(reUpdateChart);
        }
        if (vm.isMapExist) {
          elPressureMap.removeChild(elPressureMap.childNodes[0]);
          elHisPressureMap.removeChild(elHisPressureMap.childNodes[0]);
        }
        vm.isMapExist = true;
      };
    },
    isPatientExistJudge() { // 判斷病患是否在床
      const vm = this;
      vm.isPatientExist = !vm.isPatientExist;
      vm.isPatientExist ? vm.PatientExistData = 1 : vm.PatientExistData = 0;
    },
    // fake data
    getAdminInfo() { // 從 localStorage 拿假資料
      const vm = this;
      vm.admin.adminView = JSON.parse(localStorage.getItem('adminView')).adminView;
      // vm.admin.adminId = JSON.parse(localStorage.getItem('adminId')).adminId;
    },
    setAdminView() { // 根據 localStorage 的假資料判斷顯示數量
      const vm = this;
      if (vm.admin.adminView === 'group1') {
        vm.patients = [...vm.patientGroup.group1];
      } else if (vm.admin.adminView === 'group2') {
        vm.patients = [...vm.patientGroup.group2];
      } else {
        vm.patients = [...vm.patientGroup.all];
      }
    },
  },
  created() {
    this.getAdminInfo(); // 取得假資料
    this.getpatientApi();
    this.getPatinetsInfo();
  },
  mounted() {
    this.chosenPatient = this.userID;
    this.getPatientData();
  },
};
</script>

<style lang="scss">
@import "~@/scss/all.scss";
</style>
